* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  min-height: 100%;
  width: 100%;
}

body {
  font-family: arial, sans-serif;
  background: linear-gradient(0.5turn, #0d9cd9, #ba89a6, #f7860e);
}

.add-task {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 30px;
    border-bottom: 2px dashed #000000;
}

.add-task label { 
    width: 40%;
    font-size: 20px;
}

.add-task input {
    width: 60%;
    padding: 2px;
    margin-bottom: 5px;
    font-size: 20px;
}

.add-task .checkbox {
    width: 50px;
    text-align: center;
}

.add-task input[type=checkbox] {
    height: 25px;
}

.add-task button {
    margin-left: auto;
    background-color: #decece;
}

.add-task button:hover {
    color: #1b9c1b;
}


li {
    padding: 5px;
    font-size: 20px;
}

.important {
    color: red;
}

li button {
    margin-left: 10px;
    background-color: #decece;
}

li button:hover {
    color: rgb(47, 14, 191);
}
ul {
    padding: 20px;
    border-bottom: 2px dashed #000000;
    list-style-type: none;
    text-align: left;
}
.to-do-app {
    width: 80%;
    margin: 30px auto 0 auto;
    padding: 50px 30px;
    border: 4px solid #000000;
    position: relative;
    text-align: center;
    background-color: #ffba43;
}

.to-do-app h1 {
    font-size: 40px;
    text-transform: uppercase;
    margin-bottom: 40px;
}

button {
    padding: 5px 10px;
    border: 2px solid #000000;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
}

.to-do-app h3:last-child {
    width: 70%;
    padding-bottom: 5px;
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

@media (min-width: 1000px) {
    .to-do-app {
        width: 800px;
    }
}
.done-tasks {
    display: flex;
    flex-wrap: wrap;
    padding-top: 30px;
    font-size: 20px;
}

.done-tasks h3 {
    width: 100%;
    margin-bottom: 20px;
}

.done-tasks div {
    min-height: 40px;
    border-right: 2px dashed #000000;
    border-bottom: 2px dashed #000000;
    line-height: 40px;
}

.done-tasks div:nth-of-type(1), .done-tasks div:nth-of-type(2), .done-tasks div:nth-of-type(3) {
    padding-bottom: 8px;
    font-weight: bold;
} 

.done-tasks .first-column {
    width: 40%;
}

.done-tasks .second-column {
    width: 30%;
}

.done-tasks .third-column {
    width: 20%;
    border-right: 0;
}

.done-tasks button:hover {
    color: red;
}
